.movie{
    display: flex;
    background-color: white;
    padding: 10px 0px 0px 10px;
    /* border-radius: 10%; */
}

.movie_img {
    position: relative;
  top: -50px;
  max-width: 150px;
  /* width: 100%; */
  margin-right: 30px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie_info{
    padding: 0px 10px;
}

.movie_summary {
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    line-height: 16px;
    max-height: 65px;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
}

.movie_title,
.movie_year {
  margin: 0;
  font-weight: 300;
  text-decoration: none;

}

.movie_title a {
  margin-bottom: 5px;
  font-size: 24px;
  color: #000000;
  text-decoration: none;
}